"use client"

import { useState, useEffect } from "react"
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import type { Employee, EmployeeFormData, UserGroup } from "@/lib/employee-types"
import { toast } from "sonner"

interface EmployeeFormDialogProps {
  open: boolean
  onOpenChange: (open: boolean) => void
  employee?: Employee | null
  onSave: (data: EmployeeFormData) => void
  groups: UserGroup[]        // <-- Agora VEM DO BACKEND
}

export function EmployeeFormDialog({
  open,
  onOpenChange,
  employee,
  onSave,
  groups,
}: EmployeeFormDialogProps) {
  
  const isEdit = !!employee

  // 游댠 Aqui N츾O TEM MAIS MOCK
  const activeGroups = Array.isArray(groups)
    ? groups.filter(g => g.is_active)
    : []

  const [formData, setFormData] = useState<EmployeeFormData>({
    name: "",
    last_name: "",
    email: "",
    phone: "",
    cpf: "",
    group_id: "",
    hire_date: new Date(),
    status: "active",
    password: "",
    confirm_password: "",
  })

  const [errors, setErrors] = useState<Partial<Record<keyof EmployeeFormData, string>>>({})

  useEffect(() => {
    if (employee) {
      setFormData({
        name: employee.name,
        last_name: employee.last_name,
        email: employee.email,
        phone: employee.phone,
        cpf: employee.cpf || "",
        group_id: employee.group_id,
        hire_date: employee.hire_date,
        status: employee.status,
        password: "",
        confirm_password: "",
      })
    } else {
      setFormData({
        name: "",
        last_name: "",
        email: "",
        phone: "",
        cpf: "",
        group_id: "",
        hire_date: new Date(),
        status: "active",
        password: "",
        confirm_password: "",
      })
    }
    setErrors({})
  }, [employee, open])

  const validateForm = (): boolean => {
    const newErrors: Partial<Record<keyof EmployeeFormData, string>> = {}

    if (!formData.name || formData.name.trim().length < 2) {
      newErrors.name = "Nome deve ter pelo menos 2 caracteres"
    }

    if (!formData.last_name || formData.last_name.trim().length < 2) {
      newErrors.last_name = "Sobrenome deve ter pelo menos 2 caracteres"
    }

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
    if (!formData.email || !emailRegex.test(formData.email)) {
      newErrors.email = "Email inv치lido"
    }

    if (!formData.phone || formData.phone.replace(/\D/g, "").length < 10) {
      newErrors.phone = "Telefone inv치lido"
    }

    if (formData.cpf && formData.cpf.replace(/\D/g, "").length !== 11) {
      newErrors.cpf = "CPF deve ter 11 d칤gitos"
    }

    if (!formData.group_id) {
      newErrors.group_id = "Selecione um grupo"
    }

    if (!isEdit) {
      if (!formData.password || formData.password.length < 8) {
        newErrors.password = "Senha deve ter pelo menos 8 caracteres"
      }
      if (formData.password !== formData.confirm_password) {
        newErrors.confirm_password = "As senhas n칚o coincidem"
      }
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()

    if (!validateForm()) {
      toast.error("Corrija os erros no formul치rio")
      return
    }

    onSave(formData)
    onOpenChange(false)
  }

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>{isEdit ? "Editar Funcion치rio" : "Novo Funcion치rio"}</DialogTitle>
        </DialogHeader>

        <form onSubmit={handleSubmit} className="space-y-6">
          
          {/* DADOS PESSOAIS */}
          <div>
            <h3 className="text-sm font-medium mb-4">Dados Pessoais</h3>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              
              {/* Nome */}
              <div>
                <Label>Nome *</Label>
                <Input
                  value={formData.name}
                  onChange={e => setFormData({ ...formData, name: e.target.value })}
                  className={errors.name ? "border-red-500" : ""}
                />
                {errors.name && <p className="text-xs text-red-600 mt-1">{errors.name}</p>}
              </div>

              {/* Sobrenome */}
              <div>
                <Label>Sobrenome *</Label>
                <Input
                  value={formData.last_name}
                  onChange={e => setFormData({ ...formData, last_name: e.target.value })}
                  className={errors.last_name ? "border-red-500" : ""}
                />
                {errors.last_name && <p className="text-xs text-red-600 mt-1">{errors.last_name}</p>}
              </div>

              {/* Email */}
              <div>
                <Label>Email *</Label>
                <Input
                  type="email"
                  value={formData.email}
                  onChange={e => setFormData({ ...formData, email: e.target.value })}
                  className={errors.email ? "border-red-500" : ""}
                />
                {errors.email && <p className="text-xs text-red-600 mt-1">{errors.email}</p>}
              </div>

              {/* Telefone */}
              <div>
                <Label>Telefone *</Label>
                <Input
                  value={formData.phone}
                  onChange={e => setFormData({ ...formData, phone: e.target.value })}
                  className={errors.phone ? "border-red-500" : ""}
                />
                {errors.phone && <p className="text-xs text-red-600 mt-1">{errors.phone}</p>}
              </div>

              {/* CPF */}
              <div>
                <Label>CPF (opcional)</Label>
                <Input
                  value={formData.cpf}
                  onChange={e => setFormData({ ...formData, cpf: e.target.value })}
                  className={errors.cpf ? "border-red-500" : ""}
                />
                {errors.cpf && <p className="text-xs text-red-600 mt-1">{errors.cpf}</p>}
              </div>
            </div>
          </div>

          {/* DADOS PROFISSIONAIS */}
          <div>
            <h3 className="text-sm font-medium mb-4">Dados Profissionais</h3>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">

              {/* Grupo */}
              <div>
                <Label>Grupo de Acesso *</Label>
                <Select
                  value={formData.group_id}
                  onValueChange={(value) => setFormData({ ...formData, group_id: value })}
                >
                  <SelectTrigger className={errors.group_id ? "border-red-500" : ""}>
                    <SelectValue placeholder="Selecione um grupo" />
                  </SelectTrigger>

                  <SelectContent>
                    {activeGroups.map(group => (
                      <SelectItem key={group.id} value={group.id}>
                        {group.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>

                {errors.group_id && <p className="text-xs text-red-600 mt-1">{errors.group_id}</p>}
              </div>

              {/* Data de admiss칚o */}
              <div>
                <Label>Data de Admiss칚o</Label>
                <Input
                  type="date"
                  value={formData.hire_date.toISOString().split("T")[0]}
                  onChange={e => setFormData({ ...formData, hire_date: new Date(e.target.value) })}
                />
              </div>

              {/* Status */}
              <div>
                <Label>Status</Label>
                <Select
                  value={formData.status}
                  onValueChange={(value: "active" | "inactive") =>
                    setFormData({ ...formData, status: value })
                  }>
                  <SelectTrigger>
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="active">Ativo</SelectItem>
                    <SelectItem value="inactive">Inativo</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>
          </div>

          {/* SENHA */}
          <div>
            <h3 className="text-sm font-medium mb-4">
              {isEdit ? "Alterar senha (opcional)" : "Senha de acesso *"}
            </h3>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              
              <div>
                <Label>Senha</Label>
                <Input
                  type="password"
                  value={formData.password}
                  onChange={e => setFormData({ ...formData, password: e.target.value })}
                  className={errors.password ? "border-red-500" : ""}
                />
                {errors.password && <p className="text-xs text-red-600 mt-1">{errors.password}</p>}
              </div>

              <div>
                <Label>Confirmar Senha</Label>
                <Input
                  type="password"
                  value={formData.confirm_password}
                  onChange={e => setFormData({ ...formData, confirm_password: e.target.value })}
                  className={errors.confirm_password ? "border-red-500" : ""}
                />
                {errors.confirm_password && (
                  <p className="text-xs text-red-600 mt-1">{errors.confirm_password}</p>
                )}
              </div>
            </div>
          </div>

          <DialogFooter>
            <Button variant="outline" type="button" onClick={() => onOpenChange(false)}>
              Cancelar
            </Button>

            <Button type="submit">
              {isEdit ? "Salvar Altera칞칫es" : "Criar Funcion치rio"}
            </Button>
          </DialogFooter>

        </form>
      </DialogContent>
    </Dialog>
  )
}