"use client"

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { EmployeeFormDialog } from '@/components/employee-form-dialog'
import { EmployeeStatusBadge } from '@/components/employee-status-badge'
import { Plus, Search, Edit, Power, Mail, MoreVertical } from 'lucide-react'
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from '@/components/ui/dropdown-menu'
import type { Employee, EmployeeFormData, EmployeeStatus } from '@/lib/employee-types'
import { mockEmployees, mockUserGroups } from '@/lib/employee-mock-data'
import { toast } from 'sonner'

export default function FuncionariosPage() {
  const [employees, setEmployees] = useState<Employee[]>(mockEmployees)
  const [searchTerm, setSearchTerm] = useState('')
  const [filterGroup, setFilterGroup] = useState<string>('all')
  const [filterStatus, setFilterStatus] = useState<string>('all')
  const [currentPage, setCurrentPage] = useState(1)
  const [dialogOpen, setDialogOpen] = useState(false)
  const [selectedEmployee, setSelectedEmployee] = useState<Employee | null>(null)

  const itemsPerPage = 10

  // Filtros
  const filteredEmployees = employees.filter(emp => {
    const matchesSearch = 
      emp.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      emp.last_name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      emp.email.toLowerCase().includes(searchTerm.toLowerCase())
    
    const matchesGroup = filterGroup === 'all' || emp.group_id === filterGroup
    const matchesStatus = filterStatus === 'all' || emp.status === filterStatus

    return matchesSearch && matchesGroup && matchesStatus
  })

  // Pagina칞칚o
  const totalPages = Math.ceil(filteredEmployees.length / itemsPerPage)
  const startIndex = (currentPage - 1) * itemsPerPage
  const paginatedEmployees = filteredEmployees.slice(startIndex, startIndex + itemsPerPage)

  const handleCreateEmployee = () => {
    setSelectedEmployee(null)
    setDialogOpen(true)
  }

  const handleEditEmployee = (employee: Employee) => {
    setSelectedEmployee(employee)
    setDialogOpen(true)
  }

  const handleSaveEmployee = (data: EmployeeFormData) => {
    if (selectedEmployee) {
      // Editar
      setEmployees(employees.map(emp =>
        emp.id === selectedEmployee.id
          ? {
              ...emp,
              ...data,
              group_name: mockUserGroups.find(g => g.id === data.group_id)?.name,
              updated_at: new Date()
            }
          : emp
      ))
      toast.success('Funcion치rio atualizado com sucesso!')
    } else {
      // Criar
      const newEmployee: Employee = {
        id: `emp-${Date.now()}`,
        ...data,
        group_name: mockUserGroups.find(g => g.id === data.group_id)?.name,
        created_at: new Date(),
        updated_at: new Date()
      }
      setEmployees([...employees, newEmployee])
      toast.success('Funcion치rio criado com sucesso!')
    }
  }

  const handleToggleStatus = (employee: Employee) => {
    const newStatus: EmployeeStatus = employee.status === 'active' ? 'inactive' : 'active'
    setEmployees(employees.map(emp =>
      emp.id === employee.id
        ? { ...emp, status: newStatus, updated_at: new Date() }
        : emp
    ))
    toast.success(`Funcion치rio ${newStatus === 'active' ? 'ativado' : 'desativado'} com sucesso!`)
  }

  const handleResendCredentials = (employee: Employee) => {
    toast.success(`Credenciais reenviadas para ${employee.email}`)
  }

  return (
    <div className="p-8">
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4 mb-8">
        <div>
          <h1 className="text-3xl font-light text-foreground">Funcion치rios</h1>
          <p className="text-foreground/60">Gerencie os funcion치rios e seus acessos</p>
        </div>
        <Button onClick={handleCreateEmployee} className="gap-2">
          <Plus size={18} />
          Novo Funcion치rio
        </Button>
      </div>

      {/* Filtros */}
      <div className="bg-card border border-border rounded-xl p-6 mb-6">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          {/* Search */}
          <div className="relative">
            <Search size={18} className="absolute left-3 top-1/2 -translate-y-1/2 text-foreground/40" />
            <Input
              placeholder="Buscar por nome ou email..."
              value={searchTerm}
              onChange={(e) => {
                setSearchTerm(e.target.value)
                setCurrentPage(1)
              }}
              className="pl-10"
            />
          </div>

          {/* Filtro por Grupo */}
          <Select
            value={filterGroup}
            onValueChange={(value) => {
              setFilterGroup(value)
              setCurrentPage(1)
            }}
          >
            <SelectTrigger>
              <SelectValue placeholder="Todos os grupos" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">Todos os grupos</SelectItem>
              {mockUserGroups.map(group => (
                <SelectItem key={group.id} value={group.id}>
                  {group.name}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>

          {/* Filtro por Status */}
          <Select
            value={filterStatus}
            onValueChange={(value) => {
              setFilterStatus(value)
              setCurrentPage(1)
            }}
          >
            <SelectTrigger>
              <SelectValue placeholder="Todos os status" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">Todos os status</SelectItem>
              <SelectItem value="active">Ativos</SelectItem>
              <SelectItem value="inactive">Inativos</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </div>

      {/* Tabela */}
      <div className="bg-card border border-border rounded-xl overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead className="bg-muted/30 border-b border-border">
              <tr>
                <th className="px-6 py-4 text-left text-sm font-medium text-foreground">Nome</th>
                <th className="px-6 py-4 text-left text-sm font-medium text-foreground">Email</th>
                <th className="px-6 py-4 text-left text-sm font-medium text-foreground">Grupo</th>
                <th className="px-6 py-4 text-left text-sm font-medium text-foreground">Status</th>
                <th className="px-6 py-4 text-left text-sm font-medium text-foreground">Data Admiss칚o</th>
                <th className="px-6 py-4 text-right text-sm font-medium text-foreground">A칞칫es</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-border">
              {paginatedEmployees.length === 0 ? (
                <tr>
                  <td colSpan={6} className="px-6 py-12 text-center">
                    <p className="text-foreground/60">Nenhum funcion치rio encontrado</p>
                  </td>
                </tr>
              ) : (
                paginatedEmployees.map(employee => (
                  <tr key={employee.id} className="hover:bg-muted/20 transition-colors">
                    <td className="px-6 py-4">
                      <div>
                        <p className="font-medium text-foreground">
                          {employee.name} {employee.last_name}
                        </p>
                        {employee.phone && (
                          <p className="text-xs text-foreground/60">{employee.phone}</p>
                        )}
                      </div>
                    </td>
                    <td className="px-6 py-4 text-sm text-foreground/80">
                      {employee.email}
                    </td>
                    <td className="px-6 py-4">
                      <span className="inline-flex items-center px-2 py-1 rounded-md bg-primary/10 text-primary text-xs font-medium">
                        {employee.group_name || 'N/A'}
                      </span>
                    </td>
                    <td className="px-6 py-4">
                      <EmployeeStatusBadge status={employee.status} />
                    </td>
                    <td className="px-6 py-4 text-sm text-foreground/80">
                      {employee.hire_date.toLocaleDateString('pt-BR')}
                    </td>
                    <td className="px-6 py-4 text-right">
                      <DropdownMenu>
                        <DropdownMenuTrigger asChild>
                          <Button variant="ghost" size="sm">
                            <MoreVertical size={16} />
                          </Button>
                        </DropdownMenuTrigger>
                        <DropdownMenuContent align="end">
                          <DropdownMenuItem onClick={() => handleEditEmployee(employee)}>
                            <Edit size={14} className="mr-2" />
                            Editar
                          </DropdownMenuItem>
                          <DropdownMenuItem onClick={() => handleToggleStatus(employee)}>
                            <Power size={14} className="mr-2" />
                            {employee.status === 'active' ? 'Desativar' : 'Ativar'}
                          </DropdownMenuItem>
                          <DropdownMenuItem onClick={() => handleResendCredentials(employee)}>
                            <Mail size={14} className="mr-2" />
                            Reenviar Credenciais
                          </DropdownMenuItem>
                        </DropdownMenuContent>
                      </DropdownMenu>
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>

        {/* Pagina칞칚o */}
        {totalPages > 1 && (
          <div className="px-6 py-4 border-t border-border flex items-center justify-between">
            <p className="text-sm text-foreground/60">
              Mostrando {startIndex + 1} a {Math.min(startIndex + itemsPerPage, filteredEmployees.length)} de {filteredEmployees.length} funcion치rios
            </p>
            <div className="flex gap-2">
              <Button
                variant="outline"
                size="sm"
                onClick={() => setCurrentPage(p => Math.max(1, p - 1))}
                disabled={currentPage === 1}
              >
                Anterior
              </Button>
              <Button
                variant="outline"
                size="sm"
                onClick={() => setCurrentPage(p => Math.min(totalPages, p + 1))}
                disabled={currentPage === totalPages}
              >
                Pr칩xima
              </Button>
            </div>
          </div>
        )}
      </div>

      {/* Dialog */}
      <EmployeeFormDialog
        open={dialogOpen}
        onOpenChange={setDialogOpen}
        employee={selectedEmployee}
        onSave={handleSaveEmployee}
      />
    </div>
  )
}
